import { GlobalContext } from 'contents/MainFrame';
import { useContext, useEffect, useState } from 'react';
import './SamplerTab.css';

type SampleRecord = {
    // 
    soundName: string,
    // 
    isUse: boolean,
    // URL
    url: string
};

const SamplerTab = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const [samples, setSamples] = useState<SampleRecord[]>();

    useEffect(() => {
        const samples: SampleRecord[] = [];

        setSamples(samples);
    }, []);

    return (
        <div id="samplertab-root">
            // div
            <div className="loader"></div>

            // div
            <div className="list">
                <div className="record">
                    <div className="label">E3</div>
                    <input type="checkbox" />
                    <input type="text" />
                </div>
                <div className="record">
                    <div className="label">F3</div>
                    <input type="checkbox" />
                    <input type="text" />
                </div>
            </div>

            // div
            <div className="operation">
                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Save</button>

                <button
                    className={[
                        'disable'
                    ].join(' ')}
                    onClick={() => {

                    }}>Reset</button>

                <button onClick={() => {
                }}>Build</button>
            </div>
            <div className="keyboad"></div>
        </div>
    );
}

export default SamplerTab;
