import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, DIATONIC_MAJOR_SCALE_DEGREE_NAMES, DIATONIC_MINOR_SCALE_LIST, DIATONIC_NATURAL_MINOR_SCALE_DEGREE_NAMES, getChangeTypeLabel, KEY12_FLAT_LIST, KEY12_MAJOR_SCALE_LIST, KEY12_MINOR_SCALE_LIST, KEY12_SHARP_LIST, SCALE_LIST, SymbolKey, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { getChordRoot, getSymbolName } from "contents/common/utils";
import { GlobalContext } from "contents/MainFrame";
import { DetailChord, DetailFixed, DetailInit, DetailChange, DetailSection, CHORD_MODE, NoteElement, TypeNoteElement, ScoreProgress, Key12Info, ChangeType, ModulateRelation, TempoRelation } from "contents/redux/store";
import { useContext } from "react";

type ScoreElementDetailProps = {
    index: number;
    noteElement: NoteElement;
}

const ScoreElementDetail = (props: ScoreElementDetailProps) => {

    const { state, dispatcher } = useContext(GlobalContext);

    const chordMode = state.noteData.chordMode;
    const cache = state.noteState.cacheList[props.index];

    switch (props.noteElement.type) {
        case TypeNoteElement.Fixed: {
            const detail = props.noteElement.detail as DetailFixed;
            return (
                <span>{detail.label}</span>
            );
        }
        case TypeNoteElement.Init: {
            const detail = props.noteElement.detail as DetailInit;
            const paramLabels: JSX.Element[] = [];
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Tempo: </span>
                    <span>{detail.tempo}</span>
                </div>
            );
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Beat: </span>
                    <span>{detail.rythm}</span>
                </div>
            );
            if (chordMode == CHORD_MODE.SCALE) {
                paramLabels.push(
                    <div key={paramLabels.length}>
                        <span>Scale: </span>
                        <span>{detail.scale}</span>
                    </div>
                );
                paramLabels.push(
                    <div key={paramLabels.length}>
                        <span>Key: </span>
                        {/* <span>{detail.key}</span> */}
                        <span>{
                            detail.scale == SCALE_LIST[0] ?
                                KEY12_MAJOR_SCALE_LIST[detail.keyIndex] :
                                KEY12_MINOR_SCALE_LIST[detail.keyIndex]
                        }</span>
                    </div>
                );
            }
            return (
                <div>
                    {paramLabels}
                </div>
            );
        }
        case TypeNoteElement.Chord: {
            const detail = props.noteElement.detail as DetailChord;

            const degreeMolRoot = getDegreeRoot(detail.molecule);
            const degreeDenRoot = getDegreeRoot(detail.denominator);
            const chordMolRoot = getChordRoot(detail.molecule, cache.init.keyIndex);
            const chordDenRoot = getChordRoot(detail.denominator, cache.init.keyIndex);
            let symbolName = '';
            let isDiatonic = false;
            let degreeName = 'blank';
            let chordName = '-';
            if (degreeMolRoot != null) {
                symbolName = getSymbolName(detail.symbolKey);
                const diatonitChordList = cache.init.scale == 'major' ? DIATONIC_MAJOR_SCALE_DEGREE_NAMES : DIATONIC_NATURAL_MINOR_SCALE_DEGREE_NAMES;
                isDiatonic = diatonitChordList.includes(degreeMolRoot + symbolName);
                degreeName = `${degreeMolRoot}${symbolName}${degreeDenRoot == null ? '' : '/' + degreeDenRoot}`;
                chordName = `${chordMolRoot}${symbolName}${chordDenRoot == null ? '' : '/' + chordDenRoot}`;
            }
            const sustainJsx = [];
            for (let i = 0; i < detail.sustain; i++) {
                sustainJsx.push(<span key={i}></span>);
            }
            const barCount = cache.progress.barCount;
            const remaindBeat = cache.progress.remaindBeat;
            const headMSec = Math.round(cache.progress.headMSec / 1000 * 10) / 10;
            // alert(detail.degreeIndex);
            const notesCount = dispatcher.backing.getNotesCount(detail.backing);
            return (
                <>
                    <div className={
                        'degree'
                        + (detail.molecule == null ? ' blank' : '')
                        + (isDiatonic ? ' diatonic' : '')
                        // 
                        + (state.noteData.chordMode == CHORD_MODE.DIRECT ? ' visible' : '')}
                    >
                        <span>{degreeName}</span>
                    </div>
                    <div className={state.noteData.chordMode == CHORD_MODE.DIRECT ? 'degree' : 'chord'}>
                        <span>{chordName}</span>
                    </div>
                    <div className="sustain">
                        {sustainJsx}
                    </div>
                    <div className="progress-sustain">
                        <span className="bar">{barCount != -1 ? barCount : ''}</span>
                        <span className="beat">{remaindBeat != -1 ? remaindBeat : ''}</span>
                    </div>
                    <div className="progress-time">
                        <span>{headMSec}s</span>
                    </div>
                    <div
                        className={
                            'backing-script' +
                            (notesCount == 0 ? ' hidden' : '')
                        }
                    >
                        <span>notes: {notesCount}</span>
                    </div>
                </>
            );
        }
        case TypeNoteElement.Section: {
            const detail = props.noteElement.detail as DetailSection;
            return (
                <>
                    <div className="name"><span>{detail.labelName}</span></div>
                    <div className="border"></div>
                </>
            );
        }
        case TypeNoteElement.Change: {
            const detail = props.noteElement.detail as DetailChange;
            const prevCache = state.noteState.cacheList[props.index - 1];
            let changeInnerJsx = <></>;
            switch (detail.changeType) {
                case null: {
                    changeInnerJsx = (
                        <>
                            <div className="value">
                                <span>No setting</span>
                            </div>
                            <div className="result">
                                <span>***</span>]
                            </div>
                        </>
                    );
                }
                    break;
                case ChangeType.MODULATE: {
                    const relation = detail.modulateRelation === null ? 'None' : detail.modulateRelation.toString();
                    const isValuable = detail.modulateRelation === ModulateRelation.DOMINANT || detail.modulateRelation === ModulateRelation.KEY;
                    let value = 0;
                    const oldKeyIndex = prevCache.init.keyIndex;
                    const oldIsMinor = prevCache.init.scale == 'minor';
                    const newKeyIndex = cache.init.keyIndex;
                    const newIsMinor = cache.init.scale == 'minor';
                    switch (detail.modulateRelation) {
                        case ModulateRelation.DOMINANT: {
                            value = detail.domminantValue;
                        }
                            break;
                        case ModulateRelation.KEY: {
                            value = detail.keyAdjust;
                        }
                            break;
                        case ModulateRelation.RELATIVE: { } break;
                        case ModulateRelation.PARALLEL: { } break;
                    }
                    const oldKeyName = (!oldIsMinor ? KEY12_MAJOR_SCALE_LIST : KEY12_MINOR_SCALE_LIST)[oldKeyIndex];
                    const newKeyName = (!newIsMinor ? KEY12_MAJOR_SCALE_LIST : KEY12_MINOR_SCALE_LIST)[newKeyIndex];
                    changeInnerJsx = (
                        <>
                            <div className="value">
                                <span>{relation + (isValuable ? ': ' : '')}</span>
                                {isValuable ? (<span>{value}</span>) : <></>}
                            </div>
                            <div className="result">
                                <span>{oldKeyName}  {newKeyName}</span>
                            </div>
                        </>
                    );
                }
                    break;
                case ChangeType.TEMPO: {
                    const relation = detail.tempoRelation === null ? 'None' : detail.tempoRelation.toString();
                    let value = 0;
                    const oldTempo = prevCache.init.tempo;
                    const newTempo = cache.init.tempo;
                    switch (detail.tempoRelation) {
                        case TempoRelation.DIFF: {
                            value = detail.tempoDiff;
                        }
                            break;
                        case TempoRelation.RATE: {
                            value = detail.tempoRate;
                        }
                            break;
                        case TempoRelation.ABSOLUTE: {
                            value = detail.tempoAbsolute;
                        }
                            break;
                    }
                    changeInnerJsx = (
                        <>
                            <div className="value">
                                <span>{relation + ': '}</span>
                                <span>{value}</span>
                            </div>
                            <div className="result">
                                <span>{`${oldTempo}  ${newTempo}`}</span>
                            </div>
                        </>
                    );
                }
                    break;
                case ChangeType.BEAT: {
                    changeInnerJsx = (
                        <>
                        </>
                    );
                }
                    break;
            }
            if (chordMode == CHORD_MODE.SCALE) {
            }
            return (
                <>
                    <div className="change-type">
                        {/* <span>{detail.changeType === null ? '' : getChangeTypeLabel(detail.changeType)}</span> */}
                        <span>{detail.changeType === null ? 'Change' : detail.changeType.toString()}</span>
                    </div>
                    {changeInnerJsx}
                </>
            );
        }
    }
}

/** */
const getDegreeRoot = (rootInfo: Key12Info | null): string | null => {
    let name = null;
    if (rootInfo != null) {
        const list = rootInfo.isFlat ? DEGREE12_FLAT_LIST : DEGREE12_SHARP_LIST;
        name = list[rootInfo.rootIndex];
    }
    return name;
}

export default ScoreElementDetail;