
export const createFixedForm = (label: string, value: string): JSX.Element => {
    return (
        <div className="record">
            <div className="label">{label}</div>
            <div className="fixed">{value}</div>
        </div>
    );
}

export const createTextForm = (
    label: string, value: string,
    callback: (e: React.ChangeEvent<HTMLInputElement>) => void): JSX.Element => {
    return (
        <div className="record">
            <div className="label">{label}</div>
            <input type="text" value={value} onChange={(e) => callback(e)} />
        </div>
    );
}

export const createSelectForm = (
    label: string, value: string,
    list: { value: string, display: string }[],
    callback: (e: React.ChangeEvent<HTMLSelectElement>) => void): JSX.Element => {

    const optionJSXList: JSX.Element[] = [];
    list.forEach((item, i) => {
        optionJSXList.push(
            <option key={i} value={item.value}>{item.display}</option>
        );
    });
    return (
        <div className="record">
            <div className="label">{label}</div>
            <select value={value} onChange={(e) => callback(e)}>
                {optionJSXList}
            </select>
        </div>
    );
}

export const createButtonRecord = (list: { label: string, disabled: boolean, callback: () => void }[]) => {

    const buttonJSXList: JSX.Element[] = [];
    list.forEach((item, i) => {
        buttonJSXList.push(
            <button
                key={i}
                className={[item.disabled ? 'disable' : ''].join(' ')}
                onClick={() => item.callback()}
            >{item.label}</button>
        );
    });
    return (
        <div className="record">{buttonJSXList}</div>
    );
}