import { Key12Info } from "contents/redux/store";
import { Sampler } from "tone";
import { API_DOMAIN, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SymbolKey, SymbolParams, SymbolType, SYMBOL_PARAMS_LIST } from "./define";

export const initDefaultSampler = () => {
    return new Sampler({
        "E2": "https://dl.dropbox.com/s/0aghs80w9aggubw/KEPSREC040.mp3?dl=0",
        "E3": "https://dl.dropbox.com/s/wxtf0u0ly03hu44/KEPSREC052.mp3?dl=0",
        "A#3": "https://dl.dropbox.com/s/hbm9d1vo5tlxymk/KEPSREC058.mp3?dl=0",
        "E4": "https://dl.dropbox.com/s/3qy88udfkzmzr13/KEPSREC064.mp3?dl=0",
        "E5": "https://dl.dropbox.com/s/tdjp24r9pfoi2ff/KEPSREC076.mp3?dl=0",
        "E6": "https://dl.dropbox.com/s/onp7h2vn2d6nhs7/KEPSREC088.mp3?dl=0",
    }).toDestination();
}

export const loadSampler = (sample: Object) => {
    return new Sampler(sample).toDestination();
}

export const getSymbolTypeName = (symbolType: SymbolType): string => {
    return ((): string => {
        switch (symbolType) {
            case SymbolType.TRIAD: return 'Triad'
            case SymbolType.TETRAD7TH: return '7th'
            case SymbolType.TETRAD6TH: return '6th'
            case SymbolType.ADD9TH: return 'Add9th'
            case SymbolType.SIXTH_9TH: return '6th-9th'
            case SymbolType.SEVENTH_9TH: return '7th-9th'
        }
    })();
}

export const getSymbolsIncludeCategory = (symbolType: SymbolType): SymbolKey[] => {
    return SYMBOL_PARAMS_LIST.filter((symbol)=>{
        return symbolType === symbol.category;
    }).map((symbol) => symbol.key);
}

export function getSymbolParams(key: SymbolKey): SymbolParams | null {
    let symbol = null;
    SYMBOL_PARAMS_LIST.forEach((data) => {
        if (data.key == key) {
            symbol = data;
        }
    });
    return symbol;
}

export const getAdjustSusValue = (value: number): string => {
    let ret = '';
    if (value != 0) {
        if (value > 0) ret = '+' + value;
        else ret = value.toString();
    }
    return ret;
}

/** */
export const getChordRoot = (rootInfo: Key12Info | null, keyIndex: number): string | null => {
    let name = null;
    if (rootInfo != null) {
        const list = rootInfo.isFlat ? KEY12_FLAT_LIST : KEY12_SHARP_LIST;
        name = list[(rootInfo.rootIndex + keyIndex) % 12];
    }
    return name;
}

/** */
export const getSymbolName = (symbolKey: SymbolKey): string => {
    let name = '';
    SYMBOL_PARAMS_LIST.forEach((symbol) => {
        if (symbol.key == symbolKey) {
            name = symbol.name;
        }
    });
    return name;
}

export const createQueryRequestInit = (sql: string): RequestInit => {
    return {
        mode: 'cors',
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ sql })
    }
}

export const sendQueryRequestToAPI = (queryType: 'select' | 'update', sql: string): Promise<Response> => {
    return fetch(API_DOMAIN + '/' + queryType,
        createQueryRequestInit(sql)
    );
}

export const applyOnChord = (symbol: SymbolParams, molRootIndex: number, denRootIndex: number) => {

    // 
    let denInterval = denRootIndex - molRootIndex;
    if (denInterval < 0) {
        denInterval += 12;
    }
    const fixedIntervals: number[] = [];
    const fixedStruct: string[] = [];
    let isAdded = false;
    symbol.intervals.forEach((cur, i) => {
        if (i < symbol.intervals.length) {
            // const next = symbol.intervals[i + 1];
            // if(cur <= denInterval && denInterval < next) {
            if (!isAdded && denInterval < cur) {
                fixedIntervals.push(denInterval);
                fixedStruct.push('den');
                isAdded = true;
            }
            if (denInterval != cur) {
                fixedIntervals.push(cur);
                fixedStruct.push(symbol.struct[i]);
            }
        }
    });
    if (!isAdded) {
        fixedIntervals.push(denInterval);
        fixedStruct.push('den');
    }
    symbol.intervals = fixedIntervals;
    symbol.struct = fixedStruct;

}