import { GlobalContext } from "contents/MainFrame";
import { BTPattern, BTVoicing, ThemaIndexedElement, VoicingTargetCondition } from "contents/redux/store";
import { useContext, useEffect } from "react";
import { createButtonRecord, createFixedForm, createTextForm } from "./ThemaTabCommonUtil";

const ThemaTabVoicing = () => {

    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.voicing;
    const cur = dispatcher.thema.getFocusElement();

    useEffect(()=>{
        try {
            form.def = JSON.parse(form.src);
        } catch (e) {
            form.def = null;
        }
    },[form.src]);

    if (cur.type !== 'voicing') {
        return <></>
    }
    const data = dispatcher.thema.getFocusElement().reffer as BTVoicing;

    const getList = () => {
        const list:string[] = [];
        if(form.def != null)  {
            form.def.useChannels.forEach(param=>{
                list.push(`${param.octave}-${param.structIndex}`);
            });
        }
        return list.join(' ');
    }

    return (
        <div className="thematab-detail">
        {createFixedForm('Channel', getList())}
        <div className="src">
            <textarea value={form.src} onChange={(e) => {
                form.src = e.target.value;
                dispatcher.thema.setThemaState(state.themaState);
            }} />
        </div>
        {
            createButtonRecord([
                {
                    label: 'Edit',
                    disabled: false,
                    callback: () => {
                        dispatcher.thema.callBackingVoicingEditor();
                    }
                }
            ])
        }
        {
            createButtonRecord([
                {
                    label: 'Cancel',
                    disabled: state.themaState.isCreate,
                    callback: () => {

                    }
                },
                {
                    label: 'Update',
                    disabled: false,
                    callback: () => {
                        data.def = JSON.parse(form.src);
                        dispatcher.thema.setThemaeData(thema);
                        state.themaState.isCreate = false;
                        dispatcher.thema.setThemaState(state.themaState);
                    }
                },
                {
                    label: 'Delete',
                    disabled: false,
                    callback: () => {

                    }
                },
            ])
        }
        </div>
    );
}

export default ThemaTabVoicing;