import { GlobalContext } from "contents/MainFrame";
import { BTSustain, ThemaDataTree, ThemaIndexedElement } from "contents/redux/store";
import { useContext } from "react";
import { createButtonRecord, createFixedForm, createSelectForm, createTextForm } from "./ThemaTabCommonUtil";


const ThemaTabRoot = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.root;
    const cur = dispatcher.thema.getFocusElement();

    if (cur.type !== 'root') {
        return <></>
    }
    const data = dispatcher.thema.getFocusElement().reffer as ThemaDataTree;

    return (
        <div className="thematab-detail">
            {createTextForm(
                'Name',
                form.themaName,
                (e) => {
                    form.themaName = e.target.value;
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createFixedForm(
                'Children',
                String(data.sustains.length)
            )}

            {
                createButtonRecord([
                    {
                        label: 'Cancel',
                        disabled: false,
                        callback: () => {
                            form.themaName = data.themaName;
                            dispatcher.thema.setThemaState(state.themaState);
                        }
                    },
                    {
                        label: 'Update',
                        disabled: false,
                        callback: () => {
                            data.themaName = form.themaName;
                            dispatcher.thema.setThemaeData(thema);
                        }
                    },
                    {
                        label: 'Delete',
                        disabled: false,
                        callback: () => {

                        }
                    },
                ])
            }
            {
                createButtonRecord([
                    {
                        label: 'Add Sustain',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.addChildSustain();
                        }
                    }
                ])
            }

            <div className="src">
                <textarea value={form.src} onChange={(e) => {
                    form.src = e.target.value;
                    dispatcher.thema.setThemaState(state.themaState);
                }} />
            </div>
            {
                createButtonRecord([
                    {
                        label: 'Sync',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.setThemaeData(JSON.parse(form.src));
                        }
                    }
                ])
            }
        </div>
    );
}

export default ThemaTabRoot;