
import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { FloatWndState } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import './ThemaLoader.css';

const ThemaLoader = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);


    // const ref = useRef({} as HTMLDivElement);
    // useEffect(() => {
    //     dispatch(ActionType.GET_REFFER_SECTION_PALETTE, { reffer: ref.current });
    // }, []);

    const [labelName, setLabelName] = useState('Section');

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}>
            <span>Cancel</span>
        </div>
        <div
            onClick={() => {

            }}>
            <span>Enter</span>
        </div>
    </>);

    const itemJsxList: JSX.Element[] = [];
    const list: string[] = ['barrade01', 'barrade02', 'barrade03', 'pops01', 'fill-in01'];
    list.forEach((item, i) => {
        itemJsxList.push(
            <_Item key={i}>{item}</_Item>
        );
    });
    return (

        <div
            id="thema-accessor"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.THEMA_ACCESSOR ? ' active' : '')}
            onClick={() => {
            }}
        >
            <_Wrap>
                <_WrapList>
                    {itemJsxList}
                </_WrapList>
                <_WrapMatrix>

                </_WrapMatrix>
            </_Wrap>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}

export default ThemaLoader;


const _Wrap = styled.div`
    display: inline-block;
    height: 250px;
`;

const _WrapList = styled.div`
    display: inline-block;
    width: 280px;
    height: 100%;
    background-color: #000000;
    vertical-align: top;
    overflow-y: auto;
`;
const _Item = styled.div`
    display: inline-block;
    width: 100%;
    height: 30px;
    background-color: #272727;
    overflow-x: hidden;
    border: solid 1px #b4b4b4;
    font-size: 20px;
    font-weight: 600;
    color: #ffffff;
    line-height: 25px;
    padding-left: 10px;
    box-sizing: border-box;
`;

const _WrapMatrix = styled.div`
    display: inline-block;
    width: 280px;
    height: 100%;
    background-color: #868686;
    vertical-align: top;
    overflow-y: auto;
`;
