
import { getAdjustSusValue } from 'contents/common/utils';
import { GlobalContext } from 'contents/MainFrame';
import { DetailChord, TypeNoteElement } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState, VFC } from 'react';
// import './FooterEnsemble.css';

const FooterEnsemble: VFC = () => {

    const { state, dispatcher } = useContext(GlobalContext);

    const [isPlay, setPlay] = useState<boolean>(false);
    const dataList = state.noteData.dataList;
    const focusIndex = state.noteState.focusIndex;
    const dataCount = state.noteData.dataList.length;
    const info = state.noteState.ensembleInfo;

    const update = () => {
        info.audio.currentTime = info.startPosition * 0.001;
        info.audio.volume = info.volume * 0.01;
        dispatcher.score.setNoteState({
            ...state.noteState, ensembleInfo: info
        });
    }

    useEffect(() => {
        setPlay(!info.audio.paused);
    }, [info.audio.paused]);

    // const [blockJsxList, setBlockJsxList] = useState([] as JSX.Element[]);

    return (
        <div id="footer-ensemble" className="footer-child">
            <div className="record data">
                <input type="text" value={info.fileURL} onChange={(e) => {
                    info.fileURL = e.target.value;
                    info.audio.src = info.fileURL;
                    info.audio.currentTime = info.startPosition * 0.001;
                    update();
                }} />
                <input type="file" onChange={(e) => {
                    if (e.target.files != null && e.target.files[0] != null) {
                        const objectURL = window.URL.createObjectURL(e.target.files[0]);
                        info.audio.src = objectURL;
                        info.fileURL = objectURL;
                        update();
                    }
                }} />
            </div>
            <div className="record adjust">
                <input type="text" value={info.startPosition} onChange={(e) => {
                    info.startPosition = Number(e.target.value);
                    update();
                }} />
                <input type="range" min={1} max={100} value={info.volume} onChange={(e) => {
                    info.volume = Number(e.target.value);
                    update();
                }} />
            </div>
            <div className="record">
                <button
                    className={[
                        isPlay ? 'disable' : ''
                    ].join(' ')}
                    onClick={() => {
                        info.audio.play();
                        setPlay(true);
                    }}>Play</button>
                <button
                    className={[
                        !isPlay ? 'disable' : ''
                    ].join(' ')}
                    onClick={() => {
                        info.audio.pause();
                        setPlay(false);
                    }}>Stop</button>
                <button
                    className={[
                    ].join(' ')}
                    onClick={() => {
                        info.audio.currentTime = info.startPosition * 0.001;
                    }}>Reset</button>
            </div>
        </div>
    );
}

export default FooterEnsemble;