import { SymbolType } from "contents/common/define";
import { getSymbolTypeName } from "contents/common/utils";
import { GlobalContext } from "contents/MainFrame";
import { BTPattern, ThemaIndexedElement, VoicingTargetType } from "contents/redux/store";
import { useContext } from "react";
import { createButtonRecord, createSelectForm } from "./ThemaTabCommonUtil";

const ThemaTabCategory = () => {

    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.category;
    const cur = dispatcher.thema.getFocusElement();

    if (cur.type !== 'category') {
        return <></>
    }
    const data = dispatcher.thema.getFocusElement().reffer as VoicingTargetType;
    // alert('cat: ' + state.themaState.themaCacheList[state.themaState.focusIndex].type);
    return (
        <div className="thematab-detail">
            {createSelectForm(
                'Category',
                form.category == null ? '' : form.category.toString(),
                (() => {
                    const list: { value: string, display: string }[] = [];
                    list.push({ value: '', display: '' });
                    Object.values(SymbolType).filter(v => !isNaN(Number(v))).forEach((item, i) => {
                        list.push({
                            value: String(item),
                            display: getSymbolTypeName(Number(item))
                        });
                    });
                    return list;
                })(),
                (e) => {
                    form.category = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {
                createButtonRecord([
                    {
                        label: 'Cancel',
                        disabled: state.themaState.isCreate,
                        callback: () => {

                        }
                    },
                    {
                        label: 'Update',
                        disabled: false,
                        callback: () => {
                            data.symbolType = form.category as SymbolType;
                            dispatcher.thema.setThemaeData(thema);
                            state.themaState.isCreate = false;
                            dispatcher.thema.setThemaState(state.themaState);
                        }
                    },
                    {
                        label: 'Delete',
                        disabled: false,
                        callback: () => {

                        }
                    },
                ])
            }
            {
                createButtonRecord([
                    {
                        label: 'Add Condition',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.addChildCondition(cur);
                        }
                    }
                ])
            }
        </div>
    );
}

export default ThemaTabCategory;