import { GlobalContext } from "contents/MainFrame";
import { BackingPattern, BTPattern, BTSustain, ThemaIndexedElement } from "contents/redux/store";
import { useContext, useEffect } from "react";
import { createButtonRecord, createFixedForm, createTextForm } from "./ThemaTabCommonUtil";

const ThemaTabPattern = () => {

    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.pattern;
    const cur = dispatcher.thema.getFocusElement();

    useEffect(()=>{
        try {
            form.def = JSON.parse(form.src);
        } catch (e) {
            form.def = null;
        }
    },[form.src]);

    if (cur.type !== 'pattern') {
        return <></>
    }
    const data = cur.reffer as BTPattern;

    const parent = cur.parent?.reffer as BTSustain;
    

    const getChannel = () => {
        let channel = '-';
        if(form.def != null)  {
            channel = String(form.def.layers[0].table.length);
        }
        return channel;
    }

    const getNotes = () => {
        let notes = '-';
        if(form.def != null)  {
            const def: BackingPattern = JSON.parse(form.src);
            let count = 0;
            def.layers.forEach(layer => {
                layer.table.forEach(line => {
                    line.forEach(cell => {
                        cell.forEach(isUse => {
                            if (isUse) count++;
                        });
                    });
                });
            });
            notes = String(count);
        }
        return notes;
    }

    const validate = ():boolean => {
        return form.name != '' && form.def != null;
    };
    return (
        <div className="thematab-detail">
            {createFixedForm('Beat', String(parent.beat))}
            {createFixedForm('Head', String(parent.headAdjust))}
            {createFixedForm('Tail', String(parent.tailAdjust))}
            {createFixedForm('Sustain', String(parent.beat + parent.headAdjust + parent.tailAdjust))}
            {createTextForm(
                'Name',
                form.name,
                (e) => {
                    form.name = e.target.value;
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createFixedForm('Channel', getChannel())}
            {createFixedForm('Notes', getNotes())}
            <div className="src">
                <textarea value={form.src} onChange={(e) => {
                    form.src = e.target.value;
                    dispatcher.thema.setThemaState(state.themaState);
                }} />
            </div>
            {
                createButtonRecord([
                    {
                        label: 'Edit',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.callBackingPatternEditor();
                        }
                    }
                ])
            }
            {createFixedForm(
                'Children',
                String(data.tgtTypes.length)
            )}
            {
                createButtonRecord([
                    {
                        label: 'Cancel',
                        disabled: state.themaState.isCreate,
                        callback: () => {

                        }
                    },
                    {
                        label: 'Update',
                        disabled: !validate(),
                        callback: () => {
                            data.name = form.name;
                            data.def = JSON.parse(form.src);
                            dispatcher.thema.setThemaeData(thema);
                            state.themaState.isCreate = false;
                            dispatcher.thema.setThemaState(state.themaState);
                        }
                    },
                    {
                        label: 'Delete',
                        disabled: false,
                        callback: () => {

                        }
                    },
                ])
            }
            {
                createButtonRecord([
                    {
                        label: 'Add Category',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.addChildCategory(cur);
                        }
                    }
                ])
            }
        </div>
    );
}

export default ThemaTabPattern;