import './ScoreContThemaTab.css';

import { BTSustain, ThemaDataTree, BTPattern, VoicingTargetType, VoicingTargetCondition, BTVoicing, ThemaElementType, ThemaIndexedElement, FloatWndState, BackingVoicing, ChannelParam } from 'contents/redux/store';
import { GlobalContext } from 'contents/MainFrame';
import { useContext, useEffect, useState } from 'react';
import TabbedPane from 'contents/common/TabbedPane';
import ThemaTabRoot from './tabs/ThemaTabRoot';
import { KEY12_SHARP_LIST, SymbolType } from 'contents/common/define';
import ThemaTabSustain from './tabs/ThemaTabSustain';
import ThemaTabVoicing from './tabs/ThemaTabVoicing';
import ThemaTabCondition from './tabs/ThemaTabCondition';
import ThemaTabCategory from './tabs/ThemaTabCategory';
import ThemaTabPattern from './tabs/ThemaTabPattern';
import { getSymbolName, getSymbolTypeName } from 'contents/common/utils';
import BackingEditor from '../note/floatwnd/backing/BackingEditor';

const ScoreContThemaTab = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const themaState = state.themaState;
    const dataIndexies = themaState.themaCacheList;

    const [treeItemJsxList, setTreeItemJsxList] = useState<JSX.Element[]>([]);

    // 
    useEffect(() => {
        dispatcher.thema.buildIndex();
    }, [thema]);

    // JSX
    useEffect(() => {
        const treeItemJsxList: JSX.Element[] = [];

        const createTreeItemJsx = (data: ThemaIndexedElement, isActive: boolean): JSX.Element => {
            const key = treeItemJsxList.length;
            const depth = ((): number => {
                switch (data.type) {
                    case 'root': return 0;
                    case 'sustain': return 1;
                    case 'pattern': return 2;
                    case 'category': return 3;
                    case 'condition': return 4;
                    case 'voicing': return 5;
                }
            })();

            const label: JSX.Element = themaState.isCreate && key === themaState.focusIndex ?
                (<span className="create">Creating...</span>)
                : (() => {
                    switch (data.type) {
                        case 'root': {
                            const reffer = data.reffer as ThemaDataTree;
                            return (
                                <span className="value">{reffer.themaName}</span>
                            );
                        }
                        case 'sustain': {
                            const reffer = data.reffer as BTSustain;
                            return (<>
                                <span>beat: </span>
                                <span className="value">{reffer.beat}</span>
                                <span>[head: </span>
                                <span className="value">{reffer.headAdjust}</span>
                                <span>, tail: </span>
                                <span className="value">{reffer.tailAdjust}</span>
                                <span>]</span>
                            </>);
                        }
                        case 'pattern': {
                            const reffer = data.reffer as BTPattern;
                            return (<>
                                <span className="value">{reffer.name}</span>
                                <span> [channel: </span>
                                <span className="value">0</span>
                                <span>, notes: </span>
                                <span className="value">0</span>
                                <span>]</span>
                            </>);
                        }
                        case 'category': {
                            const reffer = data.reffer as VoicingTargetType;
                            return (<>
                                <span className="value">{getSymbolTypeName(reffer.symbolType as SymbolType)}</span>
                            </>);
                        }
                        case 'condition': {
                            const reffer = data.reffer as VoicingTargetCondition;
                            const min = KEY12_SHARP_LIST[reffer.minRootIndex];
                            const max = KEY12_SHARP_LIST[reffer.maxRootIndex];
                            const symbols = reffer.tgtSymbols.map((symbol) => `[${getSymbolName(symbol)}]`).join(', ');
                            return (<>
                                <span className="value">{min}</span>
                                <span></span>
                                <span className="value">{max}</span>
                                <span> {'{'}</span>
                                <span className="value">{symbols}</span>
                                <span>{'}'}</span>
                            </>);
                        }
                        case 'voicing': {
                            const reffer = data.reffer as BTVoicing;
                            let src = '';
                            if (reffer.def !== null) {
                                const list = reffer.def.useChannels;
                                const items = list.map((item) => {
                                    return `${item.octave}-${item.structIndex}`;
                                });
                                src = items.join(', ');
                            }
                            return (<>
                                <span>[</span>
                                <span className="value">{src}</span>
                                <span>]</span>
                            </>);
                        }
                    }
                })();

            const callback: Function = (() => {
                switch (data.type) {
                    case 'root': return () => dispatcher.thema.addChildSustain();
                    case 'sustain': return () => dispatcher.thema.addChildPattern(data);
                    case 'pattern': return () => dispatcher.thema.addChildCategory(data);
                    case 'category': return () => dispatcher.thema.addChildCondition(data);
                    case 'condition': return () => dispatcher.thema.addChildVoicing(data);
                }
                return () => { };
            })();
            const indentWidth = 10 + depth * 50;
            return (
                <div
                    key={key}
                    className={[
                        'item',
                        isActive ? 'active' : '',
                        themaState.isCreate && themaState.focusIndex !== key ? 'lock' : ''
                    ].join(' ')}
                >
                    <div className="indent" style={{ width: indentWidth + 'px' }}></div>
                    <div className={[
                        'opener',
                        data.ChildCnt == 0 ? 'hidden' : ''
                    ].join(' ')} onClick={() => {
                        data.isClose = !data.isClose;
                        themaState.themaCacheList = dataIndexies.slice();
                        dispatcher.thema.setThemaState(themaState);
                    }}>
                        <span>{data.isClose ? '' : ''}</span>
                    </div>
                    {((): JSX.Element => {
                        switch (data.type) {
                            case 'root': return (<div className="type root"><span>ROOT</span></div>);
                            case 'sustain': return (<div className="type sustain"><span>SUSTAIN</span></div>);
                            case 'pattern': return (<div className="type pattern"><span>PATTERN</span></div>);
                            case 'category': return (<div className="type category"><span>CATEGORY</span></div>);
                            case 'condition': return (<div className="type condition"><span>CONDITION</span></div>);
                            case 'voicing': return (<div className="type voicing"><span>VOICING</span></div>);
                        }
                    })()}
                    <div
                        className={[
                            'label',
                            themaState.focusIndex === key ? 'active' : ''
                        ].join(' ')}
                        onClick={() => {
                            themaState.focusIndex = key;
                            const cur = themaState.themaCacheList[themaState.focusIndex];
                            switch (cur.type) {
                                case 'root': {
                                    const data = cur.reffer as ThemaDataTree;
                                    const form = themaState.form.root;
                                    form.themaName = data.themaName;
                                    form.src = JSON.stringify(thema);
                                }
                                    break;
                                case 'sustain': {
                                    const data = cur.reffer as BTSustain;
                                    const form = themaState.form.sustain;
                                    form.beat = data.beat;
                                    form.headAdjust = data.headAdjust;
                                    form.tailAdjust = data.tailAdjust;
                                }
                                    break;
                                case 'pattern': {
                                    const data = cur.reffer as BTPattern;
                                    const form = themaState.form.pattern;
                                    form.name = data.name;
                                    form.def = null;
                                    form.src = JSON.stringify(data.def);
                                }
                                    break;
                                case 'category': {
                                    const data = cur.reffer as VoicingTargetType;
                                    const form = themaState.form.category;
                                    form.category = data.symbolType as SymbolType;
                                }
                                    break;
                                case 'condition': {
                                    const data = cur.reffer as VoicingTargetCondition;
                                    const form = themaState.form.condition;
                                    form.tgtSymbols = data.tgtSymbols;
                                    form.minRootIndex = data.minRootIndex;
                                    form.maxRootIndex = data.maxRootIndex;
                                }
                                    break;
                                case 'voicing': {
                                    const data = cur.reffer as BTVoicing;
                                    const form = themaState.form.voicing;
                                    form.def = null;
                                    form.src = JSON.stringify(data.def);
                                }
                                    break;
                            }
                            dispatcher.thema.setThemaState(themaState);
                        }}
                        onContextMenu={() => callback()}
                    >{label}</div>
                </div>
            )
        }

        const activeRefList: ThemaIndexedElement[] = [];
        let ref: null | ThemaIndexedElement = dataIndexies[themaState.focusIndex];
        while (ref != null) {
            activeRefList.push(ref);
            ref = ref.parent;
        }
        // for (let ref = dataIndexies[themaState.focusIndex]; ref.parent !== null; ref = ref.parent) {
        //     activeRefList.push(ref);
        // }
        // activeRefList.push(dataIndexies[0]);
        dataIndexies.forEach((data) => {
            treeItemJsxList.push(
                createTreeItemJsx(data, activeRefList.includes(data))
            );
        });

        setTreeItemJsxList(treeItemJsxList);
    }, [dataIndexies, themaState.focusIndex]);


    if (dataIndexies.length === 0) {
        return (<></>);
    }
    const cur = dataIndexies[themaState.focusIndex];

    return (
        <div id="scorecont-thematab-root">
            <BackingEditor />
            <div className="cont left">
                <div className="loader">

                </div>
                <div className={[
                    'tree',
                    themaState.isCreate ? 'lock' : ''
                ].join(' ')}>
                    {treeItemJsxList}
                </div>
                <div className="operation">

                </div>
            </div>
            <div className="cont right">

                <div id="themainfotab-root">
                    <TabbedPane groupName="themainfo-tabs"
                        tabElements={[
                            { name: 'Root', disable: true, cont: <ThemaTabRoot /> },
                            { name: 'Sustain', disable: true, cont: <ThemaTabSustain /> },
                            { name: 'Pattern', disable: true, cont: <ThemaTabPattern /> },
                            { name: 'Type', disable: true, cont: <ThemaTabCategory /> },
                            { name: 'Condition', disable: true, cont: <ThemaTabCondition /> },
                            { name: 'Voicing', disable: true, cont: <ThemaTabVoicing /> },
                        ]}
                        activeNo={(
                            (): number => {
                                switch (cur.type) {
                                    case 'root': return 0;
                                    case 'sustain': return 1;
                                    case 'pattern': return 2;
                                    case 'category': return 3;
                                    case 'condition': return 4;
                                    case 'voicing': return 5;
                                }
                            }
                        )()}
                    />
                </div>
            </div>
        </div>
    );

}

export default ScoreContThemaTab;
