import { SymbolKey, SymbolType, SYMBOL_PARAMS_LIST, TimeSignatures } from 'contents/common/define'
import { initDefaultSampler } from 'contents/common/utils'
import { Sampler } from 'tone'
import { ActionType } from './reducer'

export type Store = {
    login: LoginParams;
    dialog: Dialog;
    scoreTabIndex: number;
    noteData: NoteData;
    noteState: NoteState;
    themaDataTree: ThemaDataTree;
    themaState: ThemaState;
    reffers: Reffers;
    sampler: Sampler;
    soundUseList: number[];
    dinamicCache: null | object;
}

export type LoginParams = {
    isLogin: boolean,
    id: number,
    name: string
}

export type Dialog = {
    left: number;
    top: number;
    arg?: object;
    contents: null | JSX.Element;
}

export type ScoreCache = {
    reffer?: HTMLDivElement;
    init: DetailInit;
    progress: ScoreProgress;
}

/**
 * 
 */
export type ScoreProgress = {
    /**  */
    no: number;
    /**  */
    barCount: number;
    /**  */
    remaindBeat: number;
    /**  */
    headMSec: number;
}

export type Reffers = {
    contextMneu: HTMLDivElement;
    chordPalette: HTMLDivElement;
    sectionPalette: HTMLDivElement;
    changePalette: HTMLDivElement;
    initPalette: HTMLDivElement;
    backingPalette: HTMLDivElement;
}

export enum FloatWndState {
    NONE,
    CONTEXT_MENU,
    CHORD_PALETTE,
    SECTION_PALETTE,
    CHANGE_PALETTE,
    INIT_PALETTE,
    BACKING_PALETTE,
    SETTING_EDITOR,
    THEMA_ACCESSOR,
}

export type ChordPaletteState = {
    isAddition: boolean;
    selectedLine: ChordPaletteLine,
    // rootIndex: number;
    // isFlat: false;
    molecule: Key12Info;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
}
export enum ChordPaletteLine {
    Root, Symbol, Sustain, Adjuster
}

export type BackingEditorState = {
    target: BackingEditTarget;
    isDispVoicing: boolean;
    isEditPattern: boolean;
    molecule: Key12Info;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
    channelRowSize: number;
    useChannels: ChannelParam[];
    layers: BackingLayer[];
    isUsePedal: boolean;
    selectedLenIndex: number;
    sideIndex: number;
    selectedChannelIndex: number;
    isCorrect: boolean;
}
export type BackingEditTarget = 'note' | 'thema-pattern' | 'thema-voicing';

export type ChannelParam = {
    octave: number;
    structIndex: number;
}
export type BackingLayer = {
    side: BACKING_SIDE;
    lens: BackingLen[];
    table: boolean[][][];
    vels: number[];
}
export enum BACKING_SIDE {
    LEFT = 'LEFT',
    RIGHT = 'RIGHT'
}

export type BackingLen = {
    len: BACKING_LEN_TYPE;
    isHalf: boolean;
    isDiv3: boolean;
}

export enum BACKING_LEN_TYPE {
    LEN16 = 'LEN16',
    LEN8 = 'LEN8',
    LEN4 = 'LEN4',
    LEN2 = 'LEN2',
    LEN1 = 'LEN1',
}


export type NoteData = {
    scoreName: string;
    chordMode: CHORD_MODE;
    dataList: NoteElement[];
}

/**
 * 
 */
export enum CHORD_MODE {
    /**  */
    SCALE = 'SCALE',
    /**  */
    DIRECT = 'DIRECT'
}

export type NoteState = {
    cuScoreId: number;
    focusIndex: number;
    cacheList: ScoreCache[];
    criteriaIndex: number;
    editMode: EditMode;
    selectedList: number[];
    defaultSustain: number;
    floatwndState: FloatWndState;
    chordPaletteState: ChordPaletteState;
    backingPaletteState: null | BackingEditorState;
    backingThreads: NodeJS.Timeout[];
    ensembleInfo: EnsembleInfo;
    cloneRange: number[];
    cloneDataList: NoteElement[];
}

export type ThemaState = {
    focusIndex: number;
    focusRef: null | ThemaReffer;
    themaCacheList: ThemaIndexedElement[];
    form: ThemaForm;
    isCreate: boolean;
}

export type ThemaForm = {
    root: ThemaFormRoot;
    sustain: ThemaFormSustain;
    pattern: ThemaFormPattern;
    category: ThemaFormCategory;
    condition: ThemaFormCondition;
    voicing: ThemaFormVoicing;
}

export interface ThemaFormRoot {
    themaName: string;
    src: string;
}
export interface ThemaFormSustain {
    beat: null | number;
    headAdjust: number;
    tailAdjust: number;
}
export interface ThemaFormPattern {
    name: string;
    def: null | BackingPattern;
    src: string;
}
export interface ThemaFormCategory {
    category: null | SymbolType;
}
export interface ThemaFormCondition {
    tgtSymbols: SymbolKey[];
    minRootIndex: number;
    maxRootIndex: number;
}
export interface ThemaFormVoicing {
    def: null | BackingVoicing;
    src: string;
}

export type ThemaIndexedElement = {
    isClose: boolean;
    ChildCnt: number;
    type: ThemaElementType;
    reffer: ThemaReffer;
    parent: null | ThemaIndexedElement;
}

export type ThemaReffer = ThemaDataTree | BTSustain | BTPattern | VoicingTargetType | VoicingTargetCondition | BTVoicing;

export type ThemaElementType = 'root' | 'sustain' | 'pattern' | 'category' | 'condition' | 'voicing';

export type ThemaDataTree = {
    themaName: string;
    sustains: BTSustain[];
}

export type BTSustain = {
    beat: number;
    headAdjust: number;
    tailAdjust: number;
    patterns: BTPattern[];
}

export type BTPattern = {
    name: string;
    def: null | BackingPattern;
    tgtTypes: VoicingTargetType[];
}

export type BackingPattern = {
    layers: BackingLayer[];
    isUsePedal: boolean;
}

export type VoicingTargetType = {
    symbolType: SymbolType;
    conditions: VoicingTargetCondition[];
}

export type VoicingTargetCondition = {
    tgtSymbols: SymbolKey[];
    minRootIndex: number;
    maxRootIndex: number;
    voicings: BTVoicing[];
}

export type BTVoicing = {
    def: null | BackingVoicing;
}

export type BackingVoicing = {
    useChannels: ChannelParam[];
}

type EnsembleInfo = {
    fileURL: string;
    audio: HTMLAudioElement;
    startPosition: number;
    volume: number;
}

export enum EditMode {
    NORMAL,
    TRANSPLANT,
    CLONE,
    PREVIEW,
}

export type DetailInit = {
    tempo: number;
    rythm: string;
    keyIndex: number;
    scale: string;
}

export type DetailSection = {
    labelName: string;
}

export type DetailChord = {
    molecule: Key12Info | null;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
    backing: BackingScript;
}

export type Key12Info = {
    rootIndex: number;
    isFlat: boolean;
}

export type BackingScript = {
    useChannels: ChannelParam[];
    layers: BackingLayer[];
    isUsePedal: boolean;
}

export type DetailFixed = {
    label: string;
}

export type DetailChange = {
    changeType: null | ChangeType;
    modulateRelation: null | ModulateRelation;
    domminantValue: number;
    keyAdjust: number;
    tempoRelation: null | TempoRelation;
    tempoDiff: number;
    tempoRate: number;
    tempoAbsolute: number;
    beat: null | TimeSignatures;
}

export enum ChangeType {
    MODULATE = 'Modulate',
    TEMPO = 'Tempo',
    BEAT = 'Beat'
}

// export type ChangeModulation = {
//     relation: ModulateRelation;
//     value: number;
// }

export enum ModulateRelation {
    DOMINANT = 'Domminant',
    PARALLEL = 'Parallel',
    RELATIVE = 'Relative',
    KEY = 'Key',
}

// export type ChangeBeat = {
//     value: number;
// }

// export type ChangeTempo = {
//     relation: TempoRelation;
//     value: number;
// }

export enum TempoRelation {
    DIFF = 'Diff',
    RATE = 'Rate',
    ABSOLUTE = 'Absolute'
}

export type NoteElement = {
    type: TypeNoteElement;
    detail: DetailInit | DetailSection | DetailChord | DetailChange | DetailFixed;
}

/**
 * 
 */
export enum TypeNoteElement {
    /**  */
    Init = 'init',
    /**  */
    Section = 'section',
    /**  */
    Chord = 'chord',
    /**  */
    Change = 'Change',
    /**  */
    Fixed = 'fixed'
}

/**
 * 
 */
export type Action = {
    type: ActionType;
    payload: any;
}

/**
 * 
 */
