import { DEGREE12_SHARP_LIST, KEY12_SHARP_LIST, SCALE_LIST, SYMBOL_PARAMS_LIST } from 'contents/common/define';
import { getAdjustSusValue, getSymbolParams } from 'contents/common/utils';
import { GlobalContext } from 'contents/MainFrame';
import { DetailChord, DetailSection, TypeNoteElement, DetailChange} from 'contents/redux/store';
import { useContext } from 'react';
import './CurrentBlockDetailBar.css';

const CurrentBlockDetailBar = () => {

    const { state, dispatch } = useContext(GlobalContext);
    const focusIndex = state.noteState.focusIndex;

    if (focusIndex == -1) return (<></>);

    const curData = state.noteData.dataList[focusIndex];
    const cache = state.noteState.cacheList[focusIndex];

    let innerJsx: JSX.Element = <></>;
    switch (curData.type) {
        // 
        case TypeNoteElement.Chord: {
            const detail = curData.detail as DetailChord;
            let root = '-';
            if (detail.molecule != null) {
                const rootIndex = detail.molecule.rootIndex;
                const degreeRoot = DEGREE12_SHARP_LIST[rootIndex];
                const chordRoot = KEY12_SHARP_LIST[rootIndex];
                root = `${degreeRoot} (${chordRoot})`;
            }
            let symbolName = '-';
            if (detail.molecule != null) {
                symbolName = `[${getSymbolParams(detail.symbolKey)?.name as string}]`;
            }

            const baseBlockJsx = buildInfoBlock('Base', [
                { labelName: 'Tempo', value: '' + cache.init.tempo, disable: false },
                { labelName: 'Beat', value: cache.init.rythm, disable: false },
                { labelName: 'Key', value: KEY12_SHARP_LIST[cache.init.keyIndex], disable: false },
                { labelName: 'Scale', value: cache.init.scale, disable: false },
            ]);

            const chordBlockJsx = buildInfoBlock('Chord', [
                { labelName: 'Root', value: root, disable: root == '-' },
                { labelName: 'Symbol', value: symbolName, disable: symbolName == '-' },
                { labelName: 'On chord', value: '-', disable: true },
                { labelName: 'Sustain', value: String(detail.sustain), disable: false },
                { labelName: 'Adjsut-H', value: getAdjustSusValue(detail.headAdjust), disable: detail.headAdjust == 0 },
                { labelName: 'Adjsut-T', value: getAdjustSusValue(detail.tailAdjust), disable: detail.tailAdjust == 0 },
            ]);

            innerJsx = (
                <div
                    className={
                        'block'
                    }
                >
                    <div>
                        {baseBlockJsx}
                        {chordBlockJsx}
                        {(() => {

                            if (detail.molecule != null) {
                                const structList: JSX.Element[] = [];
                                const symbol = getSymbolParams(detail.symbolKey);
                                const list = symbol?.intervals;
                                const len = list?.length as number;
                                for (let i = 0; i < 7; i++) {
                                    if (i < len) {
                                        const interval = symbol?.intervals[i] as number;
                                        const struct = symbol?.struct[i];
                                        const sound = KEY12_SHARP_LIST[(detail.molecule.rootIndex + interval) % 12];
                                        structList.push(
                                            <div key={i} className="record">
                                                <div className="interval"><span>{interval}</span></div>
                                                <div className="struct"><span>{struct}</span></div>
                                                <div className="sound"><span>{sound}</span></div>
                                            </div>
                                        );
                                    } else {
                                        structList.push(
                                            <div key={i} className="record disable">
                                                <div className="interval"></div>
                                                <div className="struct"></div>
                                                <div className="sound"></div>
                                            </div>
                                        );
                                    }
                                };
                                return (<>
                                    <div className="title">
                                        <span>Struct</span>
                                    </div>
                                    <div className="symbol-struct">
                                        {structList}
                                    </div>
                                </>)
                            }
                            return (<></>);
                        })()}
                    </div>
                </div>
            )
        }
            break;
        // 
        case TypeNoteElement.Section: {
            const detail = curData.detail as DetailSection;

            const sectionBlockJsx = buildInfoBlock('Section', [
                { labelName: 'label', value: detail.labelName, disable: false },
                { labelName: 'box count', value: '-', disable: true },
                { labelName: 'total bar', value: '-', disable: true },
                { labelName: 'total time', value: '-', disable: true }
            ]);
            innerJsx = (
                <div
                    className={
                        'block'
                    }
                >
                    <div>
                        {sectionBlockJsx}
                    </div>
                </div>
            )
        }
            break;

        // 
        case TypeNoteElement.Change: {
            const detail = curData.detail as DetailChange;
            let changeInfo = ['-','-','-','-','-','-','-','-','-'];

            if(detail.changeType != null){
                changeInfo[0] = String(detail.changeType);
            }

            if(detail.modulateRelation != null){
                changeInfo[1] = String(detail.modulateRelation);
            }

            if(detail.domminantValue != 0){
                changeInfo[2] = String(detail.domminantValue);
            }

            if(detail.keyAdjust != 0){
                changeInfo[3] = String(detail.keyAdjust);
            }
            
            if(detail.tempoRelation != null){
                changeInfo[4] = String(detail.tempoRelation);
            }

            if(detail.tempoDiff != 0){
                changeInfo[5] = String(detail.tempoDiff);
            }

            if(detail.tempoRate != 0){
                changeInfo[6] = String(detail.tempoRate);
            }

            if(detail.tempoAbsolute != 0){
                changeInfo[7] = String(detail.tempoAbsolute);
            }

            if(detail.beat != null){
                changeInfo[8] = String(detail.beat);
            }

            const changeBlockJsx = buildInfoBlock('Change', [
                { labelName: 'type', value: changeInfo[0], disable: changeInfo[0] == '-' },
                { labelName: 'modulate', value: changeInfo[1], disable: changeInfo[1] == '-' },
                { labelName: 'domm', value: changeInfo[2], disable: changeInfo[2] == '-' },
                { labelName: 'key', value: changeInfo[3], disable: changeInfo[3] == '-' },
                { labelName: 'tempo', value: changeInfo[4], disable: changeInfo[4] == '-' },
                { labelName: 'diff', value: changeInfo[5], disable: changeInfo[5] == '-' },
                { labelName: 'rate', value: changeInfo[6], disable: changeInfo[6] == '-' },
                { labelName: 'absolute', value: changeInfo[7], disable: changeInfo[7] == '-' },
                { labelName: 'beat', value: changeInfo[8], disable: changeInfo[8] == '-' },
            ]);
            innerJsx = (
                <div
                    className={
                        'block'
                    }
                >
                    <div>
                        {changeBlockJsx}
                    </div>
                </div>
            )
        }
            break;
    }
    return (
        <div id="current-detail">
            {innerJsx}
        </div>
    );
}

export default CurrentBlockDetailBar;

/**
 * 
 */
type DataRecord = {
    /**  */
    labelName: string;
    /**  */
    value: string;
    /** true:  */
    disable: boolean;
}

/**
 * JSX
 *
 * @param {string} titleLabel 
 * @param {DataRecord[]} dataRecordList 
 * @return {*}  {JSX.Element} JSX
 */
const buildInfoBlock = (titleLabel: string, dataRecordList: DataRecord[]): JSX.Element => {
    const jsxList: JSX.Element[] = [];
    dataRecordList.forEach((state, i) => {
        jsxList.push(
            <div
                key={i}
                className={
                    'record' +
                    (state.disable ? ' disable' : '')
                }>
                <div className="title"><span>{state.labelName}:</span></div>
                <div className="value"><span>{state.value}</span></div>
            </div>
        );
    });
    return (<>
        <div className="title">
            <span>{titleLabel}</span>
        </div>
        <div className="current-state">
            {jsxList}
        </div>
    </>);
}