import { GlobalContext } from "contents/MainFrame";
import { BTSustain, ThemaIndexedElement } from "contents/redux/store";
import { useContext } from "react";
import { createButtonRecord, createFixedForm, createSelectForm } from "./ThemaTabCommonUtil";

const ThemaTabSustain = () => {
    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.sustain;
    const cur = dispatcher.thema.getFocusElement();

    if (cur.type !== 'sustain') {
        return <></>
    }
    const data = dispatcher.thema.getFocusElement().reffer as BTSustain;
    let sustain = '-';
    if (form.beat != null) {
        sustain = String(form.beat + form.headAdjust + form.tailAdjust);
    }
    const validate = form.beat != null;
    return (
        <div className="thematab-detail">
            {createSelectForm(
                'Beat',
                form.beat ? String(form.beat) : '',
                [
                    { value: '', display: '' },
                    { value: String(1), display: String(1) },
                    { value: String(2), display: String(2) },
                    { value: String(3), display: String(3) },
                    { value: String(4), display: String(4) }
                ],
                (e) => {
                    form.beat = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createSelectForm(
                'Head',
                String(form.headAdjust),
                [
                    { value: String(-0.5), display: String(-0.5) },
                    { value: String(-0.25), display: String(-0.25) },
                    { value: String(0), display: String(0) },
                    { value: String(0.25), display: String(+0.25) },
                    { value: String(0.5), display: String(+0.5) },
                ],
                (e) => {
                    form.headAdjust = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createSelectForm(
                'Tail',
                String(form.tailAdjust),
                [
                    { value: String(-0.5), display: String(-0.5) },
                    { value: String(-0.25), display: String(-0.25) },
                    { value: String(0), display: String(0) },
                    { value: String(0.25), display: String(+0.25) },
                    { value: String(0.5), display: String(+0.5) },
                ],
                (e) => {
                    form.tailAdjust = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createFixedForm('Sustain', sustain)}
            {createFixedForm(
                'Children',
                String(data.patterns.length)
            )}
            {
                createButtonRecord([
                    {
                        label: 'Cancel',
                        disabled: state.themaState.isCreate,
                        callback: () => {

                        }
                    },
                    {
                        label: 'Update',
                        disabled: !validate,
                        callback: () => {
                            data.beat = form.beat as number;
                            data.headAdjust = form.headAdjust;
                            data.tailAdjust = form.tailAdjust;
                            dispatcher.thema.setThemaeData(thema);
                            state.themaState.isCreate = false;
                            dispatcher.thema.setThemaState(state.themaState);
                        }
                    },
                    {
                        label: 'Delete',
                        disabled: false,
                        callback: () => {

                        }
                    },
                ])
            }
            {
                createButtonRecord([
                    {
                        label: 'Add Pattern',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.addChildPattern(cur);
                        }
                    }
                ])
            }
        </div>
    );
}

export default ThemaTabSustain;