import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { useContext, useState } from 'react';
import './SourceDialog.css';

const SourceDialog = (props: { target: 'score' | 'backing' }) => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    const [source, setSource] = useState<string>(JSON.stringify(state.noteData));
    return (

        <div className="source-dialog">
            <textarea value={source} onChange={(e) => {
                setSource(e.target.value);
            }}></textarea>
            <button
                onClick={() => {
                    dispatcher.closeDialog();
                }}
            >
                Cancel
            </button>
            <button
                onClick={() => {
                    switch(props.target) {
                        case 'score': {
                            dispatcher.score.setNoteData(JSON.parse(source));
                        }
                        break;
                        case 'backing': {
                            dispatcher.score.setNoteData(JSON.parse(source));
                        }
                        break;
                    }
                    dispatcher.closeDialog();
                }}
            >
                Aplly
            </button>
        </div>
    );
}

export default SourceDialog;