import { KEY12_SHARP_LIST, SymbolType } from "contents/common/define";
import { getSymbolName, getSymbolsIncludeCategory, getSymbolTypeName } from "contents/common/utils";
import { GlobalContext } from "contents/MainFrame";
import { BTPattern, ThemaIndexedElement, VoicingTargetCondition, VoicingTargetType } from "contents/redux/store";
import { useContext } from "react";
import { createButtonRecord, createFixedForm, createSelectForm, createTextForm } from "./ThemaTabCommonUtil";

const ThemaTabCondition = () => {

    const { state, dispatcher } = useContext(GlobalContext);

    const thema = state.themaDataTree;
    const form = state.themaState.form.condition;
    const cur = dispatcher.thema.getFocusElement();

    if (cur.type !== 'condition') {
        return <></>
    }
    const data = cur.reffer as VoicingTargetCondition;
    const parent = cur.parent?.reffer as VoicingTargetType;
    return (
        <div className="thematab-detail">
        {createFixedForm(
            'Category',
            getSymbolTypeName(parent.symbolType)
        )}
            {createSelectForm(
                'Root Min',
                form.minRootIndex.toString(),
                (() => {
                    const list: { value: string, display: string }[] = [];
                    for (let i = 0; i < 12; i++) {
                        list.push({
                            value: String(i),
                            display: KEY12_SHARP_LIST[i]
                        });
                    }
                    return list;
                })(),
                (e) => {
                    form.minRootIndex = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {createSelectForm(
                'Root Max',
                form.maxRootIndex.toString(),
                (() => {
                    const list: { value: string, display: string }[] = [];
                    for (let i = 0; i < 12; i++) {
                        list.push({
                            value: String(i),
                            display: KEY12_SHARP_LIST[i]
                        });
                    }
                    return list;
                })(),
                (e) => {
                    form.maxRootIndex = Number(e.target.value);
                    dispatcher.thema.setThemaState(state.themaState);
                }
            )}
            {/* {createFixedForm(
                'Symbols',
                String(data.tgtSymbols.map((symbol) => `[${getSymbolName(symbol)}]`).join(', '))
            )} */}
            <div className={['selector'].join(' ')}>
                {(() => {
                    const category: SymbolType = (cur.parent?.reffer as VoicingTargetType).symbolType;
                    return getSymbolsIncludeCategory(category).
                        map((symbol, i): JSX.Element => {
                            return (
                                <div key={i} className={[
                                    'item',
                                    form.tgtSymbols.includes(symbol) ? 'active' : ''
                                ].join(' ')}
                                    onClick={() => {
                                        if (!form.tgtSymbols.includes(symbol)) {
                                            form.tgtSymbols.push(symbol);
                                        } else {
                                            form.tgtSymbols = form.tgtSymbols.filter((item) => item !== symbol);
                                        }
                                        dispatcher.thema.setThemaState(state.themaState);
                                    }}
                                >[{getSymbolName(symbol)}]</div>
                            );
                        });
                })()}
            </div>
            {createFixedForm(
                'Children',
                String(data.voicings.length)
            )}
            {
                createButtonRecord([
                    {
                        label: 'Cancel',
                        disabled: state.themaState.isCreate,
                        callback: () => {

                        }
                    },
                    {
                        label: 'Update',
                        disabled: false,
                        callback: () => {
                            data.minRootIndex = form.minRootIndex;
                            data.maxRootIndex = form.maxRootIndex;
                            data.tgtSymbols = form.tgtSymbols;
                            dispatcher.thema.setThemaeData(thema);
                            state.themaState.isCreate = false;
                            dispatcher.thema.setThemaState(state.themaState);
                        }
                    },
                    {
                        label: 'Delete',
                        disabled: false,
                        callback: () => {

                        }
                    },
                ])
            }
            {
                createButtonRecord([
                    {
                        label: 'Add Voicing',
                        disabled: false,
                        callback: () => {
                            dispatcher.thema.addChildVoicing(cur);
                        }
                    }
                ])
            }
        </div>
    );
}

export default ThemaTabCondition;