import './ScoreContNoteTab.css';

// import TabbedPane from './common/TabbedPane.jsx';
import { ScoreCache, DetailChord, DetailInit, DetailSection, EditMode, FloatWndState, Store, TypeNoteElement, ScoreProgress, DetailChange, ChangeType, ModulateRelation, TempoRelation } from 'contents/redux/store';
import { GlobalContext } from 'contents/MainFrame';
import React, { useContext, KeyboardEvent, useEffect, useState, useRef } from 'react';

import ScoreElement, { } from './element/ScoreElement';
import { ActionType } from 'contents/redux/reducer';
import OutlineSection from './OutlineSection';
import { KeyboardShortCutAction } from '../../../../operation/InputNoteTab';
import NoteContextMenu from './floatwnd/context/NoteContextMenu';
import ChordSelector from './floatwnd/chord/ChordSelector';
import CurrentBlockDetailBar from './sideDetail/CurrentBlockDetailBar';
import BackingEditor from './floatwnd/backing/BackingEditor';
import NoteFooter from './footer/NoteFooter';
import styled from 'styled-components';
import InitEditor from './floatwnd/init/InitEditor';
import SectionEditor from './floatwnd/section/SectionEditor';
import SettingEditor from './floatwnd/setting/SettingEditor';
import ChangeEditor from './floatwnd/change/ChangeEditor';
import ThemaLoader from './floatwnd/thema/ThemaLoader';
import SourceDialog from 'contents/systab/dialog/SourceDialog';
import { sendQueryRequestToAPI } from 'contents/common/utils';

const ScoreContNoteTab = () => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    /**  */
    const [elementList, setElementList] = useState([] as JSX.Element[]);
    /**  */
    const [sectionList, setSectionList] = useState([] as JSX.Element[]);

    useEffect(() => {
        // alert(state.noteData.dataList.length);
        const dataList = state.noteData.dataList;
        // const isNormalMode = editMode == EditMode.NORMAL;
        let index = 0;
        const elementList: JSX.Element[] = [];
        const sectionList: JSX.Element[] = [];

        // const cacheList: ScoreCache[] = [];

        const cacheList = state.noteState.cacheList;
        // const cache = {} as ScoreCache;
        const cache = {} as ScoreCache;

        let no = 1;
        let totalBeat = 0;
        let totalMsec = 0;
        let lastBar = -1;
        dataList.forEach((data, i) => {

            if (data.type == TypeNoteElement.Init) {
                const init = data.detail as DetailInit;
                cache.init = { ...init };
            }

            const bar = Math.floor(totalBeat / 4) + 1;
            const progress = {} as ScoreProgress;
            progress.no = no;
            progress.barCount = lastBar != bar ? bar : -1;
            progress.remaindBeat = totalBeat % 4 > 0 ? totalBeat % 4 : -1;
            progress.headMSec = totalMsec;
            elementList.push(
                <ScoreElement
                    key={i}
                    index={i}
                    noteElement={data}
                />
            );
            if (data.type == TypeNoteElement.Chord) {
                lastBar = bar;
                const detail = data.detail as DetailChord;

                const tempo = cache.init.tempo;
                const sustain = detail.sustain + detail.headAdjust + detail.tailAdjust;
                totalBeat += sustain;
                const sectionMSec = 60000 / tempo * 4;
                totalMsec += sectionMSec / 4 * sustain;
                no++;
            } else if (data.type == TypeNoteElement.Change) {
                const detail = data.detail as DetailChange;
                switch (detail.changeType) {
                    case ChangeType.MODULATE: {
                        let newKeyIndex = cache.init.keyIndex;
                        let newIsMinor = cache.init.scale == 'minor';
                        switch (detail.modulateRelation) {
                            case ModulateRelation.DOMINANT: {
                                newKeyIndex = (24 + newKeyIndex + detail.domminantValue * 7) % 12;
                            }
                                break;
                            case ModulateRelation.KEY: {
                                const keyAdjust = Number(detail.keyAdjust);
                                newKeyIndex = (12 + newKeyIndex + keyAdjust) % 12;
                            }
                                break;
                            case ModulateRelation.RELATIVE: {
                                if (!newIsMinor) {
                                    newKeyIndex = (12 + newKeyIndex - 3) % 12;
                                } else {
                                    newKeyIndex = (12 + newKeyIndex + 3) % 12;
                                }
                                newIsMinor = !newIsMinor;
                            }
                                break;
                            case ModulateRelation.PARALLEL: {
                                newIsMinor = !newIsMinor;
                            }
                                break;
                        }

                        cache.init.keyIndex = newKeyIndex;
                        cache.init.scale = newIsMinor ? 'minor' : 'major';
                    }
                        break;
                    case ChangeType.TEMPO: {
                        let newTempo: number = cache.init.tempo;
                        switch (detail.tempoRelation) {
                            case TempoRelation.DIFF: {
                                newTempo += Number(detail.tempoDiff);
                            }
                                break;
                            case TempoRelation.RATE: {
                                newTempo = Math.floor(newTempo * detail.tempoRate / 100);
                            }
                                break;
                            case TempoRelation.ABSOLUTE: {
                                newTempo = detail.tempoAbsolute;
                            }
                                break;
                        }
                        cache.init.tempo = newTempo;
                    }
                        break;
                }
            }

            cacheList[i] = { ...cacheList[i], init: { ...cache.init }, progress };

            // 
            if (data.type == TypeNoteElement.Section) {
                sectionList.push(
                    <OutlineSection key={i} index={index} label={(data.detail as DetailSection).labelName} />
                );
                index++;
            }
        });
        setElementList(elementList);
        setSectionList(sectionList);
        dispatcher.score.setNoteState({ ...state.noteState, cacheList });

    }, [state.noteData.dataList, state.noteData.dataList.length]);

    const editMode = state.noteState.editMode;
    const isModalBlind = !(state.noteState.floatwndState == FloatWndState.NONE || state.noteState.floatwndState == FloatWndState.CONTEXT_MENU);
    return (
        <div id="scorecont-notetab-root">
            <div className="left-cont">
                <div id="score-note">
                    <div
                        id="operation-header"
                        className={[
                            [EditMode.TRANSPLANT, EditMode.CLONE].includes(editMode) ? 'mode-selector' : '',
                            [EditMode.PREVIEW].includes(editMode) ? 'mode-preview' : ''
                        ].join(' ')}
                    >
                        {/* <button className="" onClick={() => {
                            dispatch(ActionType.SELECT_SCORETAB, { index: 0 });
                        }}><span>BACK</span></button> */}
                        <button className="" onClick={() => {
                            (async () => {
                                const curScoreId = state.noteState.cuScoreId;
                                console.log('----start----');
                                const values: string[] = [];
                                    state.noteData.dataList.forEach((data, i) => {
                                        values.push(`(${curScoreId}, ${i}, '${JSON.stringify(data)}')`);
                                    });
                                    await sendQueryRequestToAPI('update', `delete from data_tbl where score = ${curScoreId}`);
                                    await sendQueryRequestToAPI('update', `insert into data_tbl values${values.join(',')}`);
                                console.log('----end----');
                                alert('The data was saved successfully!');
                            })();
                        }}><span>SAVE</span></button>
                        <button className="disable"><span>RELOAD</span></button>
                        <button className="" onClick={() => {
                            dispatcher.callDialog(50, 50, <SourceDialog target="score" />);
                        }}><span>SOURCE</span></button>
                        <button className="" onClick={() => {
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.SETTING_EDITOR })
                        }}> <span>SETTING</span></button>
                        <EditModeBorder />
                    </div>
                    <div
                        id="section-list"
                        className={[
                            [EditMode.TRANSPLANT, EditMode.CLONE].includes(editMode) ? 'mode-selector' : '',
                            [EditMode.PREVIEW].includes(editMode) ? 'mode-preview' : ''
                        ].join(' ')}
                    >
                        {sectionList}
                    </div>
                    <div
                        id="score-list-root"
                        className={[
                            [EditMode.TRANSPLANT, EditMode.CLONE].includes(editMode) ? 'mode-selector' : '',
                            [EditMode.PREVIEW].includes(editMode) ? 'mode-preview' : ''
                        ].join(' ')}
                    >
                        {elementList}
                    </div>
                    {/*  */}
                    <div id="modal-blind"
                        className={isModalBlind ? 'active' : ''}
                        onClick={() => {
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                        }}
                    />
                    {/*  */}
                    <InitEditor />
                    <NoteContextMenu />
                    <SectionEditor />
                    <ChangeEditor />
                    <ChordSelector />
                    <BackingEditor />
                    <SettingEditor />
                    <ThemaLoader />
                </div>
                <NoteFooter />

            </div>
            <div className="right-cont">
                <CurrentBlockDetailBar />
                {/* <div id="sidebar-param-selector" className="hidden" tabIndex="999">
                </div> */}
            </div>
        </div>
    );
}

const EditModeBorder = () => {
    const { state, dispatch } = useContext(GlobalContext);

    const editMode = state.noteState.editMode;
    // const isNormal = editMode == EditMode.NORMAL;
    // const setEditMode = (editMode: EditMode) => {
    //     dispatch(ActionType.SET_NOTE_STATE, { noteState: { ...state.noteState, editMode } });
    // };
    return (
        <div className="mode">
            <div className="label"><span>MODE:</span></div>
            <div className="border">
                <button
                    className={editMode == EditMode.NORMAL ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.NORMAL)}
                >
                    <span>NORMAL</span>
                </button>
                <button
                    className={editMode == EditMode.TRANSPLANT ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.BACKING)}
                >
                    <span>TRANSPLANT</span>
                </button>
                <button
                    className={editMode == EditMode.CLONE ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.BACKING)}
                >
                    <span>CLONE</span>
                </button>
                <button
                    className={editMode == EditMode.PREVIEW ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.BACKING)}
                >
                    <span>PREVIEW</span>
                </button>
            </div>
        </div>
    );
}

export default ScoreContNoteTab;

